'use client'

import { useState, useRef } from 'react'
import { Pet } from '@/lib/types'
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Download, Share2 } from 'lucide-react'
import Image from 'next/image'

interface SharePosterDialogProps {
  pet: Pet | null
  open: boolean
  onClose: () => void
}

export function SharePosterDialog({ pet, open, onClose }: SharePosterDialogProps) {
  const posterRef = useRef<HTMLDivElement>(null)

  if (!pet) return null

  const statusConfig = {
    lost: { label: 'PERDIDO', color: 'bg-red-600', datePrefix: 'Perdido em' },
    found: { label: 'ENCONTRADO', color: 'bg-blue-600', datePrefix: 'Encontrado em' },
    adoption: { label: 'PARA ADOÇÃO', color: 'bg-green-600', datePrefix: 'Disponível desde' },
  }

  const config = statusConfig[pet.status]

  const formatDate = (date: Date) => {
    return new Date(date).toLocaleDateString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
    })
  }

  const handleDownload = async () => {
    if (!posterRef.current) return

    try {
      // @ts-ignore
      const html2canvas = (await import('html2canvas')).default
      const canvas = await html2canvas(posterRef.current, {
        scale: 2,
        backgroundColor: '#ffffff',
      })

      const link = document.createElement('a')
      link.download = `poster-${pet.name || 'pet'}-${pet.id}.png`
      link.href = canvas.toDataURL('image/png')
      link.click()
    } catch (error) {
      alert('Erro ao gerar poster. Por favor, tente novamente.')
    }
  }

  const handleShare = async (platform: 'instagram' | 'whatsapp' | 'facebook') => {
    if (!posterRef.current) return

    try {
      // @ts-ignore
      const html2canvas = (await import('html2canvas')).default
      const canvas = await html2canvas(posterRef.current, {
        scale: 2,
        backgroundColor: '#ffffff',
      })

      canvas.toBlob(async (blob) => {
        if (!blob) return

        const file = new File([blob], `poster-${pet.name || 'pet'}.png`, { type: 'image/png' })
        const text = `${config.label}: ${pet.name || 'Pet sem nome'}\n${pet.breed}\n${pet.location.city}\nContato: ${pet.contactPhone}`

        if (platform === 'whatsapp') {
          const message = encodeURIComponent(text)
          window.open(`https://wa.me/?text=${message}`, '_blank')
        } else if (platform === 'facebook') {
          window.open(`https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(window.location.href)}`, '_blank')
        } else if (platform === 'instagram') {
          // Instagram doesn't support direct sharing, so we'll download and show instructions
          const link = document.createElement('a')
          link.download = `poster-${pet.name || 'pet'}-instagram.png`
          link.href = canvas.toDataURL('image/png')
          link.click()
          alert('Imagem baixada! Agora você pode compartilhar no Instagram.')
        }
      })
    } catch (error) {
      alert('Erro ao compartilhar. Por favor, tente novamente.')
    }
  }

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-sm md:max-w-md max-h-[90vh] overflow-auto">
        <DialogHeader>
          <DialogTitle>Compartilhar Anúncio</DialogTitle>
          <DialogDescription>
            Baixe o poster ou compartilhe nas redes sociais
          </DialogDescription>
        </DialogHeader>

        <div ref={posterRef} className="bg-white rounded-lg overflow-hidden border-2 border-gray-800">
          <div className={`${config.color} text-white text-center py-3`}>
            <h2 className="text-2xl font-black">{config.label}</h2>
          </div>

          <div className="p-4">
            <div className="relative aspect-square rounded-lg overflow-hidden mb-3">
              <Image
                src={pet.photoUrl || "/placeholder.svg"}
                alt={pet.name || 'Pet'}
                fill
                className="object-cover"
              />
            </div>

            {pet.name && (
              <h3 className="text-2xl font-bold text-center mb-4">{pet.name}</h3>
            )}

            <div className="space-y-2.5 text-sm">
              <div className="flex items-center justify-between">
                <span className="font-semibold">Raça:</span>
                <span>{pet.breed}</span>
              </div>

              <div className="flex items-center justify-between border-t pt-2.5">
                <span className="font-semibold">Local:</span>
                <span>{pet.location.city}</span>
              </div>

              <div className="flex items-center justify-between border-t pt-2.5">
                <span className="font-semibold">{config.datePrefix}:</span>
                <span>{formatDate(pet.lastSeenDate)}</span>
              </div>

              {pet.reward && (
                <div className="border-t pt-2.5">
                  <p className="text-center text-lg font-bold text-amber-600">RECOMPENSA: R$ {pet.reward}</p>
                </div>
              )}

              <div className="border-t pt-2.5 text-center">
                <p className="font-semibold mb-1">CONTATO</p>
                <p className="text-lg font-bold">{pet.contactPhone}</p>
                <p className="text-xs text-muted-foreground">{pet.contactName}</p>
              </div>
            </div>
          </div>
        </div>

        <div className="space-y-3">
          <Button onClick={handleDownload} className="w-full">
            <Download className="w-4 h-4 mr-2" />
            Baixar Poster (PNG)
          </Button>

          <div className="space-y-2">
            <p className="text-sm font-medium text-center">Compartilhar em:</p>
            <div className="grid grid-cols-3 gap-2">
              <Button
                onClick={() => handleShare('whatsapp')}
                className="bg-green-600 hover:bg-green-700 h-10 text-xs"
              >
                <Share2 className="w-3 h-3 mr-1" />
                WhatsApp
              </Button>
              <Button
                onClick={() => handleShare('facebook')}
                className="bg-blue-600 hover:bg-blue-700 h-10 text-xs"
              >
                <Share2 className="w-3 h-3 mr-1" />
                Facebook
              </Button>
              <Button
                onClick={() => handleShare('instagram')}
                className="bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-700 hover:to-pink-700 h-10 text-xs"
              >
                <Share2 className="w-3 h-3 mr-1" />
                Instagram
              </Button>
            </div>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}
